import React from 'react'
export default function UserCard({name, userName, repos, avatar, followers, following, bio}) {
    return (
        <div className="userCard">
            <a href={"https://www.github.com/" + userName} target="_blank" rel="noreferrer">
                <img className="" src={avatar} alt={userName}/>
            </a>
            <div className="">
                <div className="">
                    {name}
                </div>
                <div className="e">
                    {userName}
                </div>
                <div className="">
                    {repos !== '0' ? 
                    <span>Repositories: {repos}</span> : <span>Repositories doesn't exist</span>}
                </div>
                <div className="">
                    followers: {followers}
                </div>
                <div className="">
                    following:{following}
                </div>
                <div className="">
                    Biography:{bio}
                </div>
            </div>
        </div>
    );
}