import React, { useState } from 'react'
import UserSearch from './Components/UserSearch'
import UserCard from './Components/UserCard'
import UserNotFound from './Components/UserNotFound'
import { BrowserRouter as Router, Switch, Link, Route } from "react-router-dom"
import './App.css';

function App() {
  const [name, setName] = useState('');
  const [userName, setUserName] = useState('');
  const [followers, setFollowers] = useState('')
  const [following, setFollowing] = useState('')
  const [bio, setBiography] = useState('')
  const [repos, setRepos] = useState('');
  const [avatar, setAvatar] = useState('');
  const [error, setError] = useState('');

  const setUserData = function ({name, login, public_repos, avatar_url, followers, following, bio}) {
    setAvatar(avatar_url);
    setName(name);
    setUserName(login);
    setRepos(public_repos);
    setFollowers(followers);
    setFollowing(following);
    setBiography(bio);
  }

  function getUserData(text) {
    fetch('https://api.github.com/users/' + text)
    .then((res) => {
      if (res.status === 404) {
        setError(true);
        return {};
      }
      return res.json();
    })
    .then((data) => {
      setUserData(data);
    })
    .catch((err) => {
      let error = 'Data not found: ' + err;
      console.log(error);
    });
  }

  return (
    error ? <UserNotFound /> :
      <Router>
        <Switch>
          <Route exact path={process.env.REACT_APP_DEV === "true" ? "/" : "/lab9/build"}>
              <body>
              <div className="search_header">Search of Github account</div>
              <div className="github_search">
              <UserSearch MakeRequest={getUserData}/>
                {error !== '' ? <div>{error}</div> : <div></div>}
                {(avatar !== undefined && avatar !== '' && avatar.trim('/')) ? 
                  <UserCard 
                    name={name} 
                    userName={userName} 
                    repos={repos} 
                    avatar={avatar}
                    followers={followers}
                    following={following}
                    bio = {bio}/>
                :<div></div>}
              </div>
              </body>
          </Route>
        </Switch>
      </Router>
  );
}

export default App;