import React from "react";
import { BrowserRouter as Router, useHistory, Link, Route } from "react-router-dom";

const UserNotFound = () => {
  const history = useHistory();

  const handleClick = () => {
    history.push(process.env.REACT_APP_DEV === "true" ? "/" : "/lab9/build");
  };
  return (
    <button type="button" onClick={handleClick} className="back-button">
      Вернуться на страницу поиска
    </button>
  );
};

UserNotFound.propTypes = {};

export default UserNotFound;